.icons {
    animation: bounce 3s infinite linear;
    animation-timing-function: ease-in;
}

@keyframes bounce {

    15%,
    45%,
    75% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }

    0% {
        -webkit-transform: translateY(-10px);
        transform: translateY(-10px);
    }

    30% {
        -webkit-transform: translateY(-10px);
        transform: translateY(-10px);
    }

    60% {
        -webkit-transform: translateY(-10px);
        transform: translateY(-10px);
    }

    85% {
        -webkit-transform: translateY(-10px);
        transform: translateY(-10px);
    }

    90% {
        -webkit-transform: translateY(-10px);
        transform: translateY(-10px);
    }

    95% {
        -webkit-transform: translateY(-10px);
        transform: translateY(-10px);
    }

    100% {
        -webkit-transform: translateY(-10px);
        transform: translateY(-10px);
    }
}

