@media(max-width:1030px) {


    .header {
        display: none !important;
    }



    .bm-item {
        display: inline-block;
        /* Our sidebar item styling */
        text-decoration: none;
        margin-bottom: 10px;
        color: black;
        transition: color 0.2s;
    }

    .bm-item:hover {
        color: white;
    }

    .bm-burger-button {
        display: block;
        position: fixed;
        width: 24px;
        height: 22px;
        right: 18px;
        top: 42px;
    }

    /* Color/shape of burger icon bars */
    .bm-burger-bars {
        background: #d65cf1;
    }

    /* Position and sizing of clickable cross button */
    .bm-cross-button {
        height: 24px;
        width: 24px;
    }

    .bm-cross-button button {
        background: black;
    }

    /* Color/shape of close button cross */
    .bm-cross {
        background: black;
    }

    /* General sidebar styles */
    .bm-menu {
        background: #776b7a;
        padding: 2em 1em 0;
        font-size: 1.5em;
        font-family: 'Iceberg';
    }

    /* Morph shape necessary with bubble or elastic */
    .bm-morph-shape {
        fill: #373a47;
    }

    /* Wrapper for item list */
    .bm-item-list {
        color: #b8b7ad;
    }

    /* Styling of overlay */
    .bm-overlay {
        background: rgba(0, 0, 0, 0.3);
    }


    .bm-burger-button {
        display: flex !important;
    }
}

